import React from 'react'
import { ThemeProvider } from './src/context/ThemeContext'
import { DraggableProvider } from './src/context/DraggableContext'

export const wrapRootElement = ({ element }) => (
  <ThemeProvider>
    <DraggableProvider>
      {element}
    </DraggableProvider>
  </ThemeProvider>
)
