import React, { Component } from 'react'

const defaultState = {
  draggable: false,
}

const DraggableContext = React.createContext(defaultState)

class DraggableProvider extends Component {

  constructor(props) {
    super(props)
    this.state = {
      draggable: false,
    }
    this.enableDraggable.bind(this)
    this.disableDraggable.bind(this)
  }

  enableDraggable = e => {
    if (e.key === 'Shift' && !this.state.draggable) {
      console.log('enabled draggable elements')
      this.setState({ draggable: true })
    }
  }

  disableDraggable = e => {
    if (e.key === 'Shift' && this.state.draggable) {
      console.log('disabled draggable elements')
      this.setState({ draggable: false })
    }
  }

  render() {
    const { children } = this.props
    const { draggable } = this.state

    return (
      <div
        onKeyDown={this.enableDraggable}
        onKeyUp={this.disableDraggable}
      >
        <DraggableContext.Provider
          value={{
            draggable
          }}
        >
          {children}
        </DraggableContext.Provider>
      </div>
    )
  }
}

export default DraggableContext

export { DraggableProvider }
